.header-container{
    padding: 5px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tinder-icon{
    max-width: 60px;
}

.header-icon{
    font-size: 
    30px!important;
}


/* *******************  Cards section  ******************* */


.card{
    position: relative;
    /* width: 100%; */
    max-width: 600px;
    height: 65vh;
    padding:20px;
}

.card img{
    margin: 0 auto!important;
    width: 100%;
    height: 100%;
    border-radius: 30px;
    box-shadow: 0 0 23px 0 rgba(0,0,0,.3);
}

.tinder-cards{
    display: flex;
    justify-content: center;
    margin-top: 5vh;
}

.swipe{
    position: absolute;
}

.card h1{
    position: absolute;
    bottom: 15px;
    padding:10px 30px;
    font-size: 24px;
    font-weight: 600;
    color: whitesmoke;
}


.swipebuttons{
    position: absolute;
    padding: 30px 20px;
    width: 100%;
    bottom: 0;
    display: flex;
    justify-content: space-evenly;
}

.iconbutton{
    background: whitesmoke;
    padding: 1.4vw!important;
    box-shadow: 0 10px 53px 0 rgba(0,0,0,.3);
}

.swipebuttons-icons{
    font-size: 30px!important;
}

.replay{
    color: #f5b748;
}
.close{
    color: #ec5e6f;
}
.starrate{
   color:#62b4f9; 
}
.favorite{
  color: #76e2b3;
}
.flash{
    color: #915dd1;
}